a.home{margin:0}.login{text-align:center;padding:70px 0;position:relative}.login label{cursor:pointer;margin-right:15px}.c{color:#999}.prompt{position:absolute;left:10px;top:0;right:10px;padding:0 10px}.login-form{margin-bottom:25px}#token{border-radius:5px;padding:5px;width:200px;border:1px solid #CCC;outline:none;-webkit-transition:all .2s;transition:all .2s}#token:focus{box-shadow:0 1px 1px rgba(0,0,0,0.1) inset,0 0 8px rgba(49,176,213,0.9)}


